<template>
  <div>
    <div>
        <div v-for="(menu, index) in menus" :key="index">
            <!-- <div v-if="menu.show"> -->
            <div :style="paddingLeft" @click="subMenuShow[index] = !subMenuShow[index]">
                {{menu.name}}
            </div>
            <div v-if="menu.children.length > 0">
                <menu-component
                    v-if="subMenuShow[index]"
                    :menus="menu.children"
                    :isLogined="isLogined"
                    :padding="padding + 15">
                </menu-component>
            </div>
            <!-- </div> -->
        </div>
    </div>
  </div>
</template>

<script>
export default {
    name : "MenuComponent",
    props : {
        menus : Array,
        isLogined : Boolean,
        padding : Number,
    },
    data() {
        return {
            loginedUser : {},
            subMenuShow : [],
        }
    },
    mounted() {},
    methods : {},
    watch : {
        isLogined() {
            this.loginedUser = JSON.parse(localStorage.getItem("loginedUser"));
        },
        menus() {
            for(let i=0; i<this.menus.length; i++) this.subMenuShow.push(false);
        },
    },
    computed : {
        paddingLeft() {
            return {
                'margin-left' : `${this.padding}px`
            }
        },
    },
}
</script>

<style scoped>
.menuDiv {
    padding-top: 20px;
}

.bigMenu {
    padding-left: 10px;
    background-color: aliceblue;
}

.subMenu {
    padding-left: 20px;
    background-color: aqua;
}
</style>